import React, { useState } from "react"
import { Tabs, Tab } from "react-bootstrap"
import IntestazioneHelpPanel from "../../../panels/HelpSidePanel/components/IntestazioneHelpPanel"
import BadgeLegenda from "../../../panels/HelpSidePanel/components/BadgeLegenda"
import { ReactComponent as Legend01 } from "./icons/realismo_disposizione.svg"
import Legend02 from "./icons/realismo_movimentoGIF.gif"
import { ReactComponent as Legend03 } from "./icons/realismo_dettaglio.svg"
import { Link } from "react-router-dom"
import "./RealismHelp.css"

export default function RealismHelp() {
  const [key, setKey] = useState("info")
  return (
    <>
      <IntestazioneHelpPanel
        tappa={3}
        linkTappa1={"/space/phase1"}
        linkTappa2={"/space/phase2"}
        linkTappa3={"/space/phase3"}
        linkItinerario={"/itineraries#space"}
        titolo="Realismo"
        linkApprofondimento="/space/phase3/focus"
        nomeItinerario={"ITINERARIO SPAZIO"}
      />
      <Tabs
        className="mt-2"
        id="info-tabs"
        activeKey={key}
        onSelect={(k) => setKey(k)}
      >
        <Tab tabClassName="tab-info" eventKey="info" title="Spiegazione">
          <p className="mt-2">
            <strong>Di cosa si tratta</strong>
          </p>
          <p>
            Dopo aver esplorato lo spazio dellopera calviniana in molte delle
            sue principali dimensioni, la questione della rappresentazione della
            realt viene nuovamente affrontata restringendo lanalisi allo
            spazio dambientazione terrestre, localizzabile (es. Sanremo) e non
            localizzabile (es. una citt), dei testi brevi pubblicati in volume
            tra il 1945 e il 1983. Questa specifica selezione concentra il focus
            sullo spazio che possiamo definire pi realista allinterno
            dellopera dellautore, con lobiettivo di comprendere in che modo
            questo complesso insieme di figurazioni spaziali si trasformi e si
            modifichi nel corso del tempo.
          </p>
          <p>
            <strong>Come funziona</strong>
          </p>
          <div>
            La visualizzazione pu essere esplorata in due modalit, che
            presentano rispettivamente una visione dinsieme di tutti i racconti
            e una visione in dettaglio di singoli testi che lutente sceglie di
            selezionare. Nel primo caso ogni racconto  rappresentato da un
            elemento grafico lineare che si dispone in ordine cronologico di
            pubblicazione allinterno di un cerchio. Ogni linea, di lunghezza
            uniforme, rivela tre aspetti principali:{" "}
            <ol type="1">
              <li>
                il numero di ambientazioni attraverso cui si sviluppa la storia,
                rappresentate da pallini neri;{" "}
              </li>
              <li>
                la tipologia di ogni ambientazione:{" "}
                <span style={{ color: "#FFD337" }}>spazio interno</span>,{" "}
                <span style={{ color: "#00C19C" }}>spazio esterno</span>,{" "}
                <span style={{ color: "#5151FF" }}>mezzo di trasporto</span>,
                <span style={{ color: "#C6CACF" }}>
                  assenza di ambientazione
                </span>
                ;
              </li>
              <li>
                lo spostamento nello spazio, rappresentato dallanimazione che
                mostra leventuale presenza di movimento allinterno del testo.
              </li>
            </ol>
            Selezionando uno o pi elementi e scorrendo verso il basso 
            possibile avere una visione in dettaglio che permette di scoprire
            altre tre importanti caratteristiche:{" "}
            <ol type="1">
              <li>
                la lunghezza reale del racconto selezionato, particolarmente
                utile nel caso in cui si confrontino pi testi;
              </li>
              <li>la denominazione esatta di ciascun ambientazione;</li>
              <li>
                la presenza di livelli spaziali che si annidano luno dentro
                laltro, qualora ci si trovi di fronte ad ambientazioni interne
                ad altri ambienti.
              </li>
            </ol>{" "}
          </div>
          <p>
            <strong>Qualche pista di lettura</strong>
          </p>
          <p>
            Una prima importante considerazione riguarda il movimento. Mettendo
            a confronto i primi due decenni della carriera di Calvino con i
            secondi due, si assiste a unevidente inversione di tendenza: se tra
            il 1945 e il 1965 il movimento nello spazio  molto spiccato e
            soltanto una minima parte dei racconti non contempla scene di
            spostamento (7 su 73, vale a dire meno del 10%), dal 1965 in avanti
            i numeri si ribaltano a favore di un sostanziale immobilismo. Lo
            spostamento nello spazio non scompare ma si assottiglia notevolmente
            ( presente in 8 testi su 33, ossia circa nel 25% dei casi). Questo
            fenomeno  giustificato dalla nuova tendenza che emerge nella
            scrittura di Calvino a partire pi o meno dalla met degli anni
            Sessanta: lazione, lavventura e anche la carica fiabesca che
            caratterizzavano i testi partigiani, contadini o cittadini (si pensi
            a Marcovaldo, sempre intento a cimentarsi in qualche impresa)
            vengono meno, sostituiti da una forma e da un contenuto che si
            aprono piuttosto alla riflessione, alle digressioni descrittive e a
            alcuni procedimenti narrativi propri della scrittura saggistica.
            Questa inversione di tendenza non ha un riflesso diretto, come si
            potrebbe supporre, sul rapporto tra spazi esterni e spazi interni.
            Se  vero che nei primissimi anni la produzione calviniana sembra
            prediligere ambientazioni allaperto (pensiamo a racconti come <i>Paura
            sul sentiero, Uomo nei gerbidi, Campo di mine, Ultimo viene il
            corvo</i>), il sopraggiungere dei primi racconti cittadini (Visti alla
            mensa, <i>Si dorme come cani, Il gatto e il poliziotto</i>) segna molto
            presto linizio di una sostanziale oscillazione tra ambientazioni
            esterne e ambientazioni interne (o ancora, come accade in molti
            casi, miste). Tuttavia, mettendo a confronto la selezione dei
            racconti che presentano esclusivamente spazi esterni e quella dei
            racconti che presentano soltanto spazi interni e analizzando nel
            dettaglio le caratteristiche proprie di ogni testo, si nota come il
            secondo gruppo sia ragionevolmente molto omogeneo: i testi che ne
            fanno parte presentano, infatti, pochissimi ambienti e il movimento
             quasi assente. Mentre il primo si trasforma in modo sorprendente
            con il passare degli anni: le numerosissime ambientazioni dei primi
            racconti allaperto diminuiscono progressivamente fino a raggiungere
            la mono-ambientazione, passando da un movimento quasi febbrile ed
            esclusivamente orizzontale (privo, cio, di livelli spaziali che si
            annidino uno dentro laltro: vedi{" "}
            <Link to="/space/phase3/focus">Approfondimento</Link>) a un
            sostanziale immobilismo. Non stupisce, allora, che persino i mezzi
            di trasporto, di cui si trova ampia traccia nella produzione breve
            calviniana degli anni Cinquanta e Sessanta, scompaiano del tutto
            dopo il 1967.
          </p>
        </Tab>
        <Tab tabClassName="tab-info" eventKey="legenda" title="Legenda">
          <div className="mt-2">
            <div>
              <small>DISPOSIZIONE</small>
            </div>
            <div>
              <Legend01 width='288' className="mt-2" />
            </div>
          </div>
          <div className="mt-2">
            <small>COLORE</small>
            <div className="row">
              <div className="col-md-6">
                <BadgeLegenda color="#FFD337" name="Luogo interno" />
                <BadgeLegenda color="#00C19C" name="Luogo esterno" />
              </div>
              <div className="col-md-6">
                <BadgeLegenda color="#5151FF" name="Mezzo di trasporto" />
                <BadgeLegenda color="#C6CACF" name="Assenza di ambientazione" />
              </div>
            </div>
          </div>
          <div className="mt-2">
            <div>
              <small>MOVIMENTO</small>
            </div>
            <div>
              <img src={Legend02} width='200' alt="Visione in dettaglio" className="mt-2" />
            </div>
          </div>
          <div className="mt-2">
            <div>
              <small>VISIONE IN DETTAGLIO</small>
            </div>
            <div>
              <Legend03 width='291' className="mt-2" />
            </div>
          </div>
        </Tab>
      </Tabs>
    </>
  )
}
